.burgerMenuIcon {
    cursor: pointer;
}

.header {
    display: flex;
    justify-content: space-between;

    position: absolute;
    width: 100%;
    height: 60px;
    left: 0px;
    top: 0px;
    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13), 0px 0px 2px rgba(29, 33, 38, 0.1);

    padding: 20px 70px;
}

.header h1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
}